h1 {
  font-size: 1.6rem;
  line-height: 1.2;
  margin-bottom: 2rem;
}
@media (min-width: 40em) {
  h1 {
    font-size: 3rem;
  }
}

a,
a:visited,
a:active {
  text-decoration: none;
  color: #fff;
}

p {
  line-height: 1.6;
  margin: 0;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

section {
  padding-bottom: 2rem;
}

body {
  background-size: cover;
  background-image: url("../images/bg-intro-mobile.png");
  background-position: center center;
  background-repeat: no-repeat;
  background-color: #ff7a7a;
  width: 100%;
  height: 100%;
  padding: 0;
  margin: 0;
  font-size: 1rem;
  font-family: "Poppins", sans-serif;
  font-weight: 400;
  color: #fff;
}
@media (min-width: 64em) {
  body {
    background-image: url("../images/bg-intro-desktop.png");
  }
}

button {
  outline: none;
  cursor: pointer;
  border: none;
  border-radius: 0.5rem;
  padding: 1rem 2rem;
  position: relative;
}
button::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-color: rgba(185, 182, 211, 0.25);
  opacity: 0;
  transition: opacity 150ms ease-in-out;
}
button:hover::before {
  opacity: 1;
}

main {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100vw;
  min-height: 100vh;
  padding: 1rem;
}
@media (min-width: 64em) {
  main {
    flex-direction: row;
  }
}

.intro {
  max-width: 37.5rem;
}
.intro-info {
  text-align: center;
  padding: 1rem;
}
@media (min-width: 64em) {
  .intro-info {
    text-align: left;
  }
}

.form {
  padding: 1rem;
  max-width: 100%;
  width: 600px;
}
.form-header-btn {
  font-size: 1.05rem;
  background-color: #6055a5;
  color: #fff;
  line-height: 1.8;
  margin-bottom: 1.5rem;
  box-shadow: 0 5px 10px #3e3c49;
  width: 100%;
}
.form-header-btn a {
  font-weight: 400;
}
.form-header-btn a span {
  font-weight: 700;
}
.form form {
  background-color: #fff;
  padding: 1.5rem;
  border-radius: 1rem;
  width: 100%;
}
.form form .form-control {
  margin-bottom: 1rem;
  padding-bottom: 1rem;
  position: relative;
}
.form form .form-control input {
  font-weight: 700;
  border: 1px solid #b9b6d3;
  border-radius: 0.3em;
  width: 100%;
  padding: 1.2rem;
  position: relative;
}
.form form .form-control input:focus::placeholder {
  opacity: 0;
}
.form form .form-control .error-icon {
  position: absolute;
  top: 1rem;
  right: 1rem;
  bottom: 1rem;
  display: none;
}
.form form .form-control small {
  color: #ff7a7a;
  float: right;
  display: none;
}
.form form .form-control.error input {
  border: 1px solid #ff7a7a;
}
.form form .form-control.error input::placeholder {
  opacity: 0;
}
.form form .form-control.error .error-icon,
.form form .form-control.error small {
  display: block;
}
.form form .form-control.success input {
  border: 1px solid #38cc8c;
}
.form form button[type=submit] {
  background-color: #38cc8c;
  margin-bottom: 1rem;
  width: 100%;
  text-transform: uppercase;
  color: #fff;
  font-size: 0.8rem;
  font-weight: 600;
  position: relative;
}
@media (min-width: 64em) {
  .form form button[type=submit] {
    font-size: 1rem;
  }
}
.form form p {
  font-size: 0.65rem;
  color: #b9b6d3;
  text-align: center;
}
.form form p a {
  color: #ff7a7a;
  font-weight: 600;
}

.container {
  max-width: 100%;
  width: 90rem;
  margin: 0 auto;
}

.attribution {
  font-size: 11px;
  text-align: center;
}

.attribution a {
  color: #3e52a3;
}